body{
    background-color: $background;
    color: $stroke;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
h1,h2,h3,h4,h5,h6{
    font-size: 36px;
    font-family: $font-primary;
    color: $titles;
}

.status {
    display: inline-block;
    border: 1px solid #242424;
    border-radius: 3px; 
    padding: 5px 25px 9px;
    line-height: 1;

    &.status-looks-at {
        color: $verefication;
    }

    &.status-verification-failed {
        color: $errors;
    }

    &.status-verified {
        color: $accent;
    }
}

.nav {
    background-color: $titles;
    color: $background;
    height: 75px;
    width: 75px;
    border-radius: 50%;
    position: fixed;
    bottom: 50px;
    left: 50px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 800;
}

h1 {
    font-size: 58px;
    line-height: 70px;
}
h2 {
    font-size: 48px;
    line-height: 60px;
}
h3 {
    font-size: 36px;
    line-height: 34px;
}
h4 {
    font-size: 32px;
    line-height: 42px;
}
h5 {
    font-size: 24px;
    line-height: 20px;
}
h6 {
    font-size: 20px;
    line-height: 34px;
}


a {
    text-decoration: none;
    color: $accent;
    
    &:hover {
        text-decoration: underline;
    }
}

.fw-600 {
    font-family: $font-primary;
    font-weight: 600;
}

.btn{
    display: inline-block;
    border: 1px solid $stroke;
    border-radius: 5px;
    color: $titles;
    font-size: 18px;
    font-weight: 600;
    

    &.btn-primary{
        padding: 9px 61px;
        background-color: $accent;
        border-color: $accent-hover;
        color: $system;

        &:hover,
        &:active{
            border-color: $accent-hover;
            background-color: $accent-hover;
        }

        &:active,
        &:focus, 
        &:not(.btn-check)+.btn:active {
            color: $titles;
        }
    }

   &.btn-secondary{
        @extend .btn-primary;
        padding: 9px 52px;
        background-color: transparent;
        border-color: $stroke;
        color: $titles;

        &:hover {
            color: $system;
        }

        &:active,
        &:focus,
        &:not(.btn-check)+.btn:active {
            color: $titles;
        }
   }

    &.btn-lager{
        border-radius: 8px;
        padding: 27px 96px;
    }

    &.btn-profile{
        border-radius: 8px;
        padding: 8px 8px;
        width: 56px;
        height: 56px;
        position: relative;

        &:hover {
            border-color: $body-text;
        }

        &.verefied,
        &.no-verefied {
            &::before {
                content: "";
                position: absolute;
                width: 12px;
                height: 12px;
                right: -6px;
                top: -6px;
                border-radius: 50%;
                background-color: $accent;
            }
        }
        
        &.no-verefied{
            &::before{
                background-color: $errors;
            }
        }

        svg {
            width: 18px;
            height: 16px;
        }
    }

    &.icon {
        border-radius: 8px;
        padding: 8px 8px;
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #141810;
        border: none;
        
        svg {
            width: 22px;
            height: 23px;
        }
    }

    

    &:focus-visible{
        box-shadow: none;
    }

    &:not(.btn-check)+.btn:active {
        color: inherit;
        background-color: $accent-hover;
        border-color: $accent-hover;
    }
}

